@font-face {
  font-family: "Futuran";
  src:url("../../assets/font/FuturaMdBTMedium.ttf");
}
  body {
    background: #FFFFFF;
    color: #1b1b32;
    font-family: Helvetica;
    margin: 0;
    font-family: "Futuran";
  }

  header {
    z-index: 1000;
    width: 100%;
    height: 85px;
    /*background-color: #000000;*/
    background-color: #797572;
    background-image:
        linear-gradient(-173deg, rgba(255,255,255,0.20) 0%, #000000 100%),
        linear-gradient(72deg, rgba(255,255,255,0.25) 25%, rgba(0,0,0,0.25) 100%),
        radial-gradient(closest-side at 47% 102%, rgba(255,255,255,0.50) 0%, rgba(21,24,32,0.60) 120%);
    background-blend-mode: multiply;
    display: flex;
    justify-content: space-around;
    align-items: center;
    position: fixed;
    top: 0;
  }

  #logo {
    width: max(10rem, 18vw);
    aspect-ratio: 25 / 4;
    padding: 0.4rem;
    cursor:pointer
  }
  #logo1 {
    color: #FFFFFF;
    font-size: 24px;
    padding: 0.4rem;
    cursor:pointer
  }

  nav {
    width: 50%;
    max-width: 300px;
    height: 50px;
  }

  nav > ul {
    display: flex;
    justify-content: space-evenly;
    flex-wrap: wrap;
    align-items: center;
    padding-inline-start: 0;
    margin-block: 0;
    height: 100%;
  }

  nav > ul > li {
    color: #FFFFFF;
    margin: 0 0.2rem;
    padding: 0.2rem;
    display: block;
  }

  nav > ul > li:hover {
    background: linear-gradient(90deg, rgba(247, 73, 212), rgba(90, 37, 234), rgba(50, 196, 245), rgba(96, 255, 165), rgba(226, 255, 62), rgba(255, 196, 77), rgba(255, 102, 77));
    -webkit-text-fill-color: transparent;
    -webkit-background-clip: text;
    cursor: pointer;
  }
  .nav-link.active {
    background: linear-gradient(90deg, rgba(247, 73, 212), rgba(90, 37, 234), rgba(50, 196, 245), rgba(96, 255, 165), rgba(226, 255, 62), rgba(255, 196, 77), rgba(255, 102, 77));
    -webkit-text-fill-color: transparent;
    -webkit-background-clip: text;/* 选中时的颜色 */
  }
  li > a {
    color: inherit;
    text-decoration: none;
  }

  main {
    padding-top: 50px;
  }

  footer {
    height: 5vw;
    background-color: #2a2a40;
    display: flex;
    justify-content: center;
  }

  address {
    color: #FFFFFF;
    text-align: center;
    padding: 0.3em;
    font-style: normal;
  }
  main{
    margin-top: 30px;
  }
.music-a {
  font-size: 1.5vw !important;
  font-weight: normal !important;
  color: #000000;
  position: relative;
  text-decoration: none;
  transition: all 0.3s ease;
}

.music-a::after {
  content: '';
  position: absolute;
  width: 0;
  height: 2px;
  background-color: #000000;
  bottom: 0;
  left: 0;
  transition: width 600ms ease
}

.music-a:hover::after {
  width: 100%;
  transform-origin: left;
}

.nav-link:hover{
  font-size: 1.5vw ;
  font-weight: 700 ;
}

  a, a:hover {
    transition: all .2s ease-in-out;
    text-decoration: none;
}
.background-img {
  position: relative; /* 使伪元素定位相对于此元素 */
  max-width: 100%;
  height: 100vh; /* 保持图片的纵横比 */
  display: block;
  margin: 0 auto;
  padding: 9vw 8vw 0 8vw;
}
.background-video{
  position: absolute;
  height: 100%;
  width: 100%;
  object-fit: cover;
  z-index: -999;
  filter: brightness(0.7); /* 调整亮度，值越小越暗 */
}
.video-container {
  position: relative;
  width: 100%;
  height: 100vh; /* 保持视频高度与视口一致 */
  overflow: hidden;
}
.background-img::before {
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-image: url("http://img.hongyoubizhi.com/picture/pages/original/2022/08/31/13/101601312_p0.jpg");
  background-size: cover; /* 覆盖整个背景区域 */
  background-repeat: no-repeat; /* 不重复背景图片 */
  background-position: center; /* 图片居中 */
  filter: brightness(0.7); /* 调整亮度，值越小越暗 */
  z-index: -1; /* 确保背景图片在内容下方 */
}
.music-new{
  width: 65vw;
  height: 30vw;
  margin: 9vw auto;/* 视频背景 */
  /*margin: 0 auto;!* 图片背景 *!*/
  display: flex;
  justify-content: center;
  gap: 7vw;
}
/*首页更新图片*/
.music-new-img{
  width: 100%;
  height: 100%;
}

/*图片容器*/
.music-new-div{
  width: 26vw;
  height: 26vw;
  position: relative;
}
.music-new-div::before {
  content: '';
  position: absolute;
  left: -2%;
  top: -2%;
  width: 104%; /* calc方法可以用来动态计算长度 */
  height: 104%;
  /*border-radius: 10px;*/
  z-index: -1;
  background: repeating-conic-gradient(from var(--range), #0ff,#f0f,#f00,#ff0,#0f0,#0ff);
  opacity: 0; /* 初始状态透明 */
  animation: fadeIn 1s forwards 1s, rotating 10s linear infinite 1s; /* 顺序：fadeIn 动画，rotating 动画 */
}
@keyframes fadeIn {
  from {
    opacity: 0;
  }
  to {
    opacity: 1;
  }
}
@property --range {
  initial-value: 0deg;
  syntax: '<angle>';
  inherits: false;
}
@keyframes rotating {
  0% {
    --range: 0deg;
  }
  100% {
    --range: 360deg;
  }
}

