button::before {
  content: "";
  /* 按钮内部闪光块的宽高、颜色 */
  width: 70%;
  height: 100%;
  background-color: rgba(255, 255, 255, 0.1);
  position: absolute;
  top: 0;
  left: -10px;
  /* 倾斜闪光块 */
  transform: skewX(-15deg);
  /* 动画时间 */
  transition: 0.5s;
}
button:hover:before {
  /* 鼠标放上去之后改变位置，模拟闪光的动画 */
  left: 102%;
}

/* 小一点的闪光块 */
button::after {
  content: "";
  /* 按钮内部闪光块的宽高、颜色 */
  width: 50px;
  height: 100%;
  background-color: rgba(255, 255, 255, 0.1);
  /* 定位靠后一点 */
  position: absolute;
  top: 0;
  left: -70px;
  /* 倾斜闪光块 */
  transform: skewX(-15deg);
  /* 动画时间 */
  transition: 0.5s;
  /* 延时动画，比上面大的闪光块晚一点出去 */
  transition-delay: 0.2s;
}
button:hover:after {
  /* 和上面一样，鼠标放上去改变位置，可以写在一起 */
  left: 102%;
}

.music-new-message-button{
  font-size: 0.7vw;
  height: 1.5vw;
  text-align: center;
  border: 0;
  color: #FFFFFF;
  background-color: #212121;
  overflow: hidden;
}
.admission-top-under{
    opacity: 0; /* 初始状态透明 */
    transform: translateY(-100%); /* 从上方100%的位置开始 */
    animation: slideInFromTop 1.5s forwards; /* 动画名称、持续时间、填充模式 */
}
.admission-under-top{
    opacity: 0; /* 初始状态透明 */
    transform: translateY(100%); /* 从下方100%的位置开始 */
    animation: slideInFromBottom 1.5s forwards; /* 动画名称、持续时间、填充模式 */
}
@keyframes slideInFromTop {
  from {
    opacity: 0;
    transform: translateY(-100%);
  }
  to {
    opacity: 1;
    transform: translateY(0);
  }
}
@keyframes slideInFromBottom {
  from {
    opacity: 0;
    transform: translateY(100%);
  }
  to {
    opacity: 1;
    transform: translateY(0);
  }
}
.works{
  padding: 1vw 10vw 0;
  margin-bottom: 60px;
}
.music-flex{
  margin-top: 1vw;
  display: flex;
  gap: 5vw;
