}

.pointer-border{
  position: absolute;
  --l: 2vw;/* 边框长度 */
  --g: 8px;/* 边框间隔 */
  --t: 3px;/* 边框厚度 */
  --s: 17vw;/* 图片尺寸 */
  --x: 0px;/* 边框坐标 */
  --y: 0px;/* 边框坐标 */
  width: calc(var(--s) + var(--g) * 2);
  height: calc(var(--s) + var(--g) * 2);
  border: var(--t) solid #c2c2c2;
  z-index: -999;
  left: calc(var(--x) - var(--g));
  top: calc(var(--y) - var(--g));
  transition: 0.5s;
  --mask:conic-gradient(at var(--l) var(--l), transparent 75%,#c2c2c2 75%) 0 0 / calc(100% - var(--l)) calc(100% - var(--l));
  mask: var(--mask);
  -webkit-mask: var(--mask);
}

.explode{
  width: 17vw;
  height: 17vw;
  /*border: #c2c2c2 1px solid;*/
  overflow: hidden; /* 隐藏超出部分 */
  justify-content: center; /* 水平居中 */
  align-items: center;     /* 垂直居中 */
  /*border-radius: 4px;*/
}
.img-thumbnail{
  height: 100%;
  width: 100%;
  object-fit: cover; /* 保持图片比例并覆盖整个容器 */
  transition: transform 1s, filter 2s; /* 平滑过渡效果 */
}
.img-thumbnail:hover{
  cursor:url(../../assets/mouse/link.cur), pointer!important;
  transform: scale(1.1); /* 放大1.1倍 */
  filter: blur(2px);     /* 模糊2像素 */
}
.overlay-box{
  position: relative;
}

.overlay {
  position: absolute;
  bottom: 1vw;
  left: 1vw;
  color: white; /* 文字颜色 */
  font-size: 1vw; /* 文字大小 */
  letter-spacing: 0.1em;
  z-index: 999;
}

.overlay span {
  display: block;
  margin: -2px 0; /* 调整间距 */
  opacity: 0;
  transform: translateX(-100%); /* 默认从左侧移出 */
  transition: transform 0.5s, opacity 0.5s;
}
.overlay span.text-uppercase-author{
  font-size: 1vw;
  font-weight: bold;
  transition-delay: 0s; /* 第一行文字无延迟 */
}
.overlay span.text-uppercase-work{
  font-size: 0.9vw;
  transition-delay: 0.3s; /* 第二行文字延迟0.5秒 */
}
.explode:hover .overlay span {
  opacity: 1;
  transform: translateX(0); /* 平滑入场 */
}

/*音频播放*/
.audio-music{
  z-index: 1000;
  position: fixed;
  bottom: 0;
}

/* 隐藏滚动条 */
div::-webkit-scrollbar {
  display: none;
}
/* 设置音频样式 */
audio {
  width: 100vw; /* 宽度为100% */
  height: 3vw; /* 高度为50px */
  border-radius: 10px; /* 边框半径为10px */
  background-color: rgba(255, 255, 255, 0.3);
}

/* 设置音频播放进度条的颜色 */
audio::-webkit-media-controls-progress-bar {
  background-color: #000;
}

/* 隐藏音频播放器的背景颜色 */
audio::-webkit-media-controls-enclosure {
  background-color: rgba(216, 218, 221, 0.0);
}
